#s1 {
	width: 100%;
	border-top: 1px solid #aaa;
	height: 600px;
	position: relative;
}
.f_img {
	min-width: 980px;
	max-width: 1920px;
	height: 600px;
	background: url(../img/bg_visual_1.jpg);
	background-size: cover;
	margin: 0 auto;
}
.f_container {
	width: 980px;
	margin: 0 auto;
	position: relative;
}
.f_txt {
	position: absolute;
	font-size: 44px;
	font-weight: bold;
	padding: 25px;
	top: 60px;
}
.f_circle {
	position: absolute;
	z-index: 5;
	top: 200px;
	left: 30px;
}
.f_wrap {
	width: 980px;
	height: 80px;
	background: rgba(0,0,0,.5);
	position: absolute;
	top: 490px;
	a {
		color: #fff;
		font-weight: bold;
		display: block;
	}
	img {
		vertical-align: middle;
		margin-right: 5px;
	}
	li {
		float: left;
		padding: 25px 7px;
		&:first-child {
			padding: 25px 7px 20px 16px;
		}
		&:last-child {
			background: url(../img/bg_quickBar01.gif) no-repeat 100% 50%;
		}
	}
}
.f_form {
	margin: 30px 0 0;
	position: relative;
	width: 245px;
	float: left;
	p {
		color: #fff;
		float: left;
		font-size: 14px;
		font-weight: bold;
		margin: 2px 10px;
		float: left;
	}
}
.f_f_txt {
	width: 135px;
	height: 40px;
	border-radius: 3px;
	border: none;
	margin: -10px 0;
	box-sizing: border-box;
	padding: 10px 5px;
	float: left;
	font-size: 14px;
}
.f_f_img {
	position: absolute;
	right: 20px;
	top: 3px;
	outline: none;
}
.f_co {
	display: block;
	width: 120px;
	height: 40px;
	background: url(../img/btn_search01.gif);
	float: left;
	margin: 20px 0;
}